/* typography */
@include font-face("CentraNo2", "../../fonts/CentraNo2", 500, normal, otf);
@include font-face(
  "Clearface-Regular",
  "../../fonts/Clearface-regular",
  500,
  normal,
  otf
);

body {
  font-family: $font-primary;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  font-size: $body-text-size;
  color: $black;
  background-color: $color-dark;
  line-height: 1.6;
}

p,
.paragraph {
  margin: 0;
  line-height: 1.7;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  margin: 0;
  line-height: 1.4;
}

h4,
h5,
h6 {
  font-weight: 700;
}

h1,
.h1 {
  font-size: $h1;
  @include tablet {
    font-size: $h1-md;
  }
}

h2,
.h2 {
  font-size: $h2;
  @include tablet {
    font-size: $h2-md;
  }
}

h3,
.h3 {
  font-size: $h3;
  @include tablet {
    font-size: $h3-md;
  }
}

h4,
.h4 {
  font-size: $h4;
}

h5,
.h5 {
  font-size: $h5;
}

h6,
.h6 {
  font-size: $h6;
}
